.modal__body {
  height: 100%;
}
.modal__close-button {
  cursor: pointer;
  position: absolute;
  top: 4rem;
  right: 5%;
  display: flex;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  box-shadow: 0 0 3px 1px rgb(87, 87, 255);
  transition: scale 0.4s ease-in-out;
}
.modal__close-button:hover {
  scale: 1.3;
}
.modal__close-button .icon-angles-down-solid {
  color: rgb(87, 87, 255);
}
.modal__image {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6rem 0;
  height: 100vh;
  opacity: 0;
  transition: opacity 0.7s ease-in-out 1s;
